@media screen and (max-width: 768px) {

    //TOP NAVIGATION
    top-navigation {
        .top-navigation {
            transition: height 0.5s ease-in-out;
            width: 100%;

            .aria-label {
                @include flex-box--row();
                padding: 8px 25px;

                .logo-mini {
                    display: inline-block;
                    background-image: url('../../assets/images/logo_ndc.png');
                    @include bg-image();
                    width: 65px;
                    height: 65px;
                }

                button {
                    display: block;
                    height: 40px;
                    width: 40px;
                    background-color: transparent;
                    border: 1px solid #0E6F40;

                    .svg-inline--fa {
                        cursor: pointer;
                        color: $color--lighter--green;
                        width: 50%;
                        height: 50%;
                    }
                }
            }

            .aria-visible {
                display: initial;
            }


            .top-navigation-container {
                @include flex-box--row();
                position: initial;
                margin: 20px 25px;
                background-color: #def0d5;

                .top-navigation--header {
                    display: none;
                }

                .top-navigation--links {
                    border: none;
                    height: initial;

                    ul {
                        @include flex-box--column();

                        li {
                            margin: 20px 0;
                        }
                    }
                }

                .social-media-links {
                    @include flex-box--column();

                    a {
                        margin: 15px 0;

                        .svg-inline--fa {
                            cursor: pointer;
                            width: 25px !important;
                            height: 25px !important;
                            color: $color--darker--green;
                        }
                    }
                }
            }



            .aria-hidden {
                display: none;
            }
        }
    }

    /*
        HOME SECTION
    */
    main {
        home-page {

            //hero
            hm-hero {
                .hero-container {
                    .hero--overlay--container {
                        position: relative;
                        top: 150px;
                        width: 230px;
                        margin: 0 auto;
                        left: 0;
                    }
                }
            }

            //mission
            hm-mission {
                .mission-container {
                    height: 800px;
                }
            }

            //primary
            hm-primary {
                .primary-bg-container {
                    .primary-container {
                        .primary-container--top {

                            .pri-container--top--img_left {
                                background-size: contain;
                                border-radius: 10px;
                                width: 500px;
                                height: 400px;
                                margin: auto;
                            }

                            .pri-container--top-text_right {
                                p {
                                    text-align: center;
                                }
                            }
                        }


                        //bottom image and text
                        .primary-container--bottom {
                            .pri-container--bottom--text_left {
                                width: 100%;
                                text-align: center;
                                margin: 60px 0;
                            }

                            .pri-container--bottom--img_right {
                                width: 100%;
                                height: 300px;
                                margin: 0;
                                background-size: contain;
                            }
                        }
                    }
                }
            }

            //commitment
            hm-commitment {
                .info--commit {
                    padding: 0 25px;
                }
            }

            //yt
            hm-yt {
                .yt-section-container {
                    .yt-grid-container {

                        //images
                        .yt-grid--images {
                            width: 100%;
                            grid-template-rows: repeat(3, 300px);
                            grid-template-columns: none;

                            .yt-grid--img_1 {
                                background-size: cover;
                            }

                            .yt-grid--img_2 {
                                background-size: cover;
                            }

                            .yt-grid--img_3 {
                                background-size: cover;
                            }
                        }
                    }
                }
            }

            //timeline
            hm-timeline {
                .timeline-section {

                    /* Place the timelime to the left */
                    .timeline-container::after {
                        display: none;
                    }


                    .timeline-container {
                        padding: 0 25px;
                        margin: 0 auto;

                        .timeline-bubble-sections {
                            padding: 0 30px;


                            .timeline {
                                position: initial;
                                margin: 0 auto;
                            }

                            /* Make sure that all arrows are pointing leftwards */
                            .timeline::before {
                                display: none;
                            }

                            .timeline::after {
                                display: none;
                            }

                            .timeline-bubble--left {
                                margin-bottom: 30px;

                                .timeline-bubble--left_content {
                                    margin: 0;
                                }
                            }

                            .timeline-bubble--right {
                                .timeline-bubble--right_content {
                                    margin: 0;
                                    text-align: left;
                                }

                            }

                            // .timeline-bubble--right::after {
                            //     left: -359px;
                            // }

                            // .timeline-bubble--right::before {
                            //     left: -315px;
                            // }

                        }
                    }


                }
            }


            /* Make all right containers behave like the left ones */
            .timeline-bubble--right {
                left: 0%;
            }

        }
    }
}


