                    .hero--content_text {
                        h1 {
                            font-size: 10vmin !important;
                        }
                    }


                    .hero--overlay--container {
                        position: relative;
                        top: 60px;
                        width: 230px;
                        margin: 0 auto;
                        left: 0;
                    }
                }
            }

            //yt-section
            // //yt
            hm-yt {
                .yt-section-container {
                    .yt-grid-container {
                        .yt-grid--link {
                            border: none;
                            height: 365px;
                            cursor: pointer;
                            width: auto;

                            //join us...
                            .yt-grid--link_header {
                                margin: 30px 0;
                                height: 60px;

                                h2 {
                                    font-size: 20px;
                                    text-align: center;
                                    color: $color--darker--green;
                                    font-family: 'Inter-Medium', sans-serif;
                                }
                            }

                            .yt-grid--link_holder {
                                margin: 0 50px;
                                width: 300px;
                                height: 300px;

                                display: flex;
                                justify-content: center;
                                align-items: center;

                                .svg-inline--fa {
                                    cursor: pointer;
                                }
                            }
                        }
                    }
                }
            }


        }
    }
}

