@media screen and (max-width: 320px) {
    top-navigation {
        .top-navigation {


            .aria-visible {
                display: initial;
            }


            .top-navigation-container {
                padding: 0 15px;
                background-color: #def0d5;

                .social-media-links {
                    @include flex-box--row();
                    justify-content: center;
                    margin: 35px 0;

                    a {
                        margin: 0 10px;

                        .svg-inline--fa {
                            cursor: pointer;
                            width: 25px !important;
                            height: 25px !important;
                            color: $color--darker--green;
                        }
                    }
                }
            }

            .aria-hidden {
                display: none;
            }
        }
    }

    main {
        home-page {

            //mission
            hm-mission {
                .mission-container {
                    height: 1200px;
                }
            }

        }
    }
